.period__cta{
    border: 2px solid white;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;

    padding: 2rem;
    margin: 4rem;
    border-radius: 1rem;
    background: var(--gradient-bar);
}

.period__cta-content h3{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 24px;
    line-height: 45px;
    color: black;
}

.period__cta-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 2rem;
}

.period__cta-btn button{
    background: black;
    border-radius: 40px;
    color: white;
    font-family: var(--font-family);
    font-weight: bold;
    font-size: 18px;
    line-height: 30px;
    padding: 0.5rem 1rem;
    border: none;
    outline: none;
    cursor: pointer;
    min-width: 150px;
}

 @media screen and (max-width:650px) {
    .period__cta{
        flex-direction: column;
    }
    .period__cta-btn{
        margin: 2rem 0 0;
    }
 }
 @media screen and (max-width:550px) {
    .period__cta{
        margin: 4rem 2rem;
    }
    .period__cta-content h3{
        font-size: 18px;
        line-height: 32px;
    }
    .period__cta-btn button{
        font-size: 14px;
        line-height: 28px;
    }
 }